.Navbar_nav__2m0rn {
    grid-area: n;

    background-color: black;
    text-transform: uppercase;
    text-align: center;
    padding: 40px 0;
}


.Navbar_item__2jmES a {
    display: inline-block;
    width: 100%;
    color: #fff;
    text-decoration: none;
    --initialTextColor: #FFF;
    --slideTextColor: black;

    --initialBgColor: black;
    --slideBgColor: #FFF;

    color: var(--initialTextColor);

    background-image: linear-gradient(90deg,
            var(--noneBgColor) 0%,
            var(--noneBgColor) 50%,
            var(--slideBgColor) 50%,
            var(--slideBgColor) 100%);

    background-image: linear-gradient(90deg,
            var(--initialBgColor) 0%,
            var(--initialBgColor) 50%,
            var(--slideBgColor) 50%,
            var(--slideBgColor) 100%);
    background-size: 200%;
    transition: background-position .3s cubic-bezier(.47, .1, 1, .63),
        color .2s linear;
    transition-delay: 0.0s, 0.15s;
}

.Navbar_item__2jmES a:hover {
    font-size: 20px;
    color: var(--slideTextColor);
    cursor: pointer;
    background-position: -100% 100%;
}


.Navbar_ava_nav_item__2zJ3N {
